@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* base styles */
* {
    margin: 0;
}

body, a {
    
    font-family: "Quicksand";
    color: #333;
}

.navbar {

    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    max-width: 600px;
    margin: 0 auto;
    border-bottom: 1px solid #ffafc7;
}

.navbar h1 {
    color: #f1356d;
}

.navbar .links {
    margin-left: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.navbar a {

    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
}

.navbar a:hover {
    color: #f1356d;
}

.content {

    max-width: 600px;
    margin: auto;
    padding: 20px;
}


/* Warning */
.warning {

    margin-bottom: 35px;
    padding: 10px 20px;
    border: 1px solid #f1356d;
    border-radius: 20px;
    background: #ffb7cc;
    font-family: sans-serif;
    line-height: 1.4em;
}

.warning p {
    margin: 10px 4px;
}

.warning a {

    color: #ff004c;
    font-weight: 600;
    text-decoration: underline;
}

.warning span {

    padding: 2px 4px;
    border-radius: 6px;
    background: #555555;
    color: white;
    font-size: 16px;
}


/* blog previews/list */
.blog-preview {

    margin: 20px 0;
    border-bottom: 1px solid #dbdbdb54;
    border-radius: 5px;
}

.blog-preview a > div {

    padding: 10px 16px;
}

.blog-preview:hover {
    box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.blog-preview h2 {
    
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 8px;
}

.blog-list a {
    text-decoration: none;
}


/* blog details page */
.blog-details h2 {
    
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 10px;
}

.blog-details .blog-body {
    margin: 20px 0;
}

.blog-details button {

    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
}


/* create new blog form */
.create {

    max-width: 400px;
    margin: 0 auto;
    text-align: center;
}

.create label {

    text-align: left;
    display: block;
}

.create h2 {

    font-size: 20px;
    color: #f1356d;
    margin-bottom: 30px;
}

.create input,
.create textarea,
.create select {

    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
}

.create button {

    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
}

/* 404 - Not Found Page */
.not-found h2 {

    color: #f1356d;
    margin-bottom: 30px;
}

.not-found a {
    color: #f1356d;
}
.skeleton {

    background: #ddd;
    overflow: hidden;
    margin: 10px 0;
    border-radius: 4px;
}

.skeleton.pink {
    background: #ff427b3b;
}

.skeleton.text {

    width: 100%;
    height: 12px;
}

.skeleton.small-text {

    width: 25%;
    height: 12px;
}

.skeleton.title {

    width: 75%;
    height: 20px;
    margin-bottom: 15px;
}

.skeleton.avatar {

    margin-right: 10px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.skeleton-wrapper {

    position: relative;
    margin: 20px 0;
    padding: 10px 15px;
    border-bottom: 1px solid #dbdbdb54;
    border-radius: 5px;
    overflow: hidden;
}

.blog-details .skeleton-wrapper {

    padding: 0;
    border-bottom: none;
}

.skeleton-author {

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

/* Animation Effects */
.shimmer-wrapper {

    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transform: translate(100%);
            transform: translate(100%);
    -webkit-animation: loading 1s infinite;
            animation: loading 1s infinite;
}

.shimmer {

    width: 50%;
    height: 100%;
    background-color: #ffffff33;
    -webkit-transform: skewX(-20deg);
            transform: skewX(-20deg);
}

@-webkit-keyframes loading {
    
    0% { -webkit-transform: translateX(-50%); transform: translateX(-50%) }
    100% { -webkit-transform: translateX(100%); transform: translateX(100%) }
}

@keyframes loading {
    
    0% { -webkit-transform: translateX(-50%); transform: translateX(-50%) }
    100% { -webkit-transform: translateX(100%); transform: translateX(100%) }
}
